import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import {mapGetters, mapState} from "vuex";

export default {
    name: "about",
    components: {
        "app-tab-nav": appTabNav
    },
    data() {
        return {
            list: [],
            tabList: [
                {id:1, name: '优惠券'},
                {id:2, name: '商品'}
            ],
            page: 2,
            activeTab: 1,
        };
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        })
    },
    onLoad() { this.$commonLoad.onload();
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.getList();
    },
    methods: {
        tabStatus(e) {
            this.list = [];
            this.page = 2;
            this.activeTab = +e.currentTarget.dataset.id;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.getList();
        },
        toOrder(id) {
            uni.navigateTo({
                url: '/pages/order/order-detail/order-detail?id=' + id
            });
        },
        toList() {
            uni.navigateTo({
                url: '/pages/coupon/index/index'
            });
        },
        getList() {
            let that = this;
            let url = that.$api.integral_mall.coupon_order;
            if(that.activeTab == 2) {
                url = that.$api.integral_mall.order
            }
            that.$request({
                url: url,
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
    },
    onReachBottom() {
            let that = this;
            let url = that.$api.integral_mall.coupon_order;
            if(that.activeTab == 2) {
                url = that.$api.integral_mall.order
            }
            that.$request({
                url: url,
                data: {
                    page: that.page
                }
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    let more = false;
                    that.list.forEach(function(row,index){
                        if(row.id == response.data.list[0].id) {
                            more = true;
                            return false
                        }
                    })
                    if(!more) {
                        that.list = that.list.concat(response.data.list);
                    }
                    page++;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
    }
}
